body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
}

h1 {
  margin-bottom: 20px;
  font-size: 24px;
}

.loader-wrapper {
  text-align: center;
  margin-bottom: 20px;
  display: none;
}

.cssload-loader {
  width: 40px;
  height: 40px;
  border: 4px solid #f3f3f3;
  border-top-color: #3498db;
  border-radius: 50%;
  margin: 0 auto;
  animation: cssload-spin 2s linear infinite;
  display: inline-block;
}

@keyframes cssload-spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.error-wrapper {
  color: #a94442;
  background-color: #f2dede;
  border: 1px solid #ebccd1;
  border-radius: 4px;
  margin-bottom: 20px;
  padding: 10px;
  display: none;
}

#cat-info {
  margin-bottom: 20px;
  display: none;
}

#cat-image {
  width: 100%;
  max-width: 400px;
  margin-bottom: 10px;
}

#cat-info p {
  margin-bottom: 10px;
}

